<template>
	<div id="" class="vip">
		<div class="vip-bg">
				<img class="avatar" src="../../assets/shops/imgs/img-gift.png"/>
				<div>123</div>
				<div class="state">官方合伙人</div>
				
			</div>
		<ul class="tabs2">
			<li>
				<p>当月业绩(元)</p>
				<div>{{month_yeji}}</div>
			</li>
			<li>
				<p>全部业绩(元)</p>
				<div>{{z_yeji}}</div>
			</li>
			<li>
				<p>直推人数(人)</p>
				<div>100</div>
			</li>
		</ul>
		<div class="my-team">
			<div class="tit">我的团队</div>
			<div id="title">
				<div>头像</div>
				<div>姓名</div>
				<div>等级</div>
			</div>
			<ul>
				<li v-for="item in items">
					<div class="headerimg"><img :src="item.headimgurl"/></div>
					<div>{{item.nickname}}</div>
					<div>{{item.type}}</div>
				</li>
				
			</ul>
		</div>
	</div>
</template>

<script>
export default {
    data (){
      return {
      	month_yeji:'',
      	z_yeji:'',
          items:[
			{src:'../../../static/03.jpg',name:'张三',state:'官方创始人'},
			{src:'../../../static/03.jpg',name:'张三',state:'官方创始人'},
			]
      }
    },
    methods: {
      getTeamInfo(page){
    	this.post('home/User/getTeamInfo',{page:page,number:20}).then(function(res){  	
    		if(res.data.code===10000){
    			this.items=res.data.info.data;    		
    			
//  			this.items.forEach(function(item){
//  				item.headimgurl=this.imageURL(item.headimgurl)
//  				
//  			}.bind(this))
    		}
//			console.log(res)
    	}.bind(this))
     },
     getUserYeji(){
     	this.post('home/User/getUserYeji', {}).then(function(res) {
     		console.log(res)
          if (res.data.code === 10000) {
            this.month_yeji = res.data.month_yeji;
            this.z_yeji = res.data.z_yeji;
          }
        }.bind(this))
     }
    },
    mounted(){
      this.getTeamInfo(1)
      this.getUserYeji(1)
    }
  }	
</script>

<style>
.my-team{margin: .2rem;margin-top: .5rem; background: #fff;padding: 0 .2rem;border-radius: 4px;}
.my-team .tit{border-bottom: 1px #e6e6e6 solid;padding: .2rem 0;}

.vip{width: 100%;margin:auto ;}
.vip .vip-bg {background: linear-gradient(to right, #FF6984 , #FF4660);width:100%;height: 4.5rem;
background-size:7.1rem 3.67rem ;text-align: center;margin:0 auto .2rem ;padding: .4rem 0;color: #fff;}
.vip .vip-bg .avatar{width:1.5rem ;height: 1.5rem;border-radius: 50%;}
.vip .vip-bg .state{text-align: center;font-size: .24rem;width: 2.5rem;padding: .1rem 0;
border: 1px #fff solid;margin:.2rem auto .35rem;border-radius: .8rem;}
.vip .vip-bg .state:before{content: ' ';display: inline-block;margin-right: .15rem;vertical-align: middle;
background: url(../../../static/imgs/shops/icons/vip-white.png) no-repeat;width: .3rem;height: .3rem;background-size: .3rem .3rem;}

.tabs2{padding: .4rem 0;background: #fff;margin: .2rem;display: flex;text-align: center;border-radius: 4px;margin-top: -1rem;}
.tabs2 li{flex: 1;}
.tabs2 li p{color: #999;}
.tabs2 li div{font-size: .4rem;}

#title {display: flex;;}
#title div{flex: 1;color:#FF5773 ;}
.my-team ul{padding-bottom: .2rem;margin-top: .5rem;}
.my-team img{width: .6rem;height: .6rem;display: block;}
.my-team li{display: flex;margin-top: .2rem;vertical-align: middle;align-items: center;}
.my-team div{flex: 1;}
.my-team ul div{color: #999;}
</style>


